import * as React from "react";

import { ChatFilterModes } from "../../../types";
import Action from "../../../redux/types/actionTypes";
import Filter from "./Filter";

type Props = {
    nameFilterInput: string,
    filterMode: ChatFilterModes,
    updateNameFilter: (input: string) => Action,
    cleanNameFilter: () => Action,
    setFilterMode: (mode: ChatFilterModes) => Action,
}

const Navigation: React.FC<Props> = props => {
    const {nameFilterInput, filterMode, updateNameFilter, cleanNameFilter,
    setFilterMode} = props;
    return (
        <Filter
        nameFilterInput={nameFilterInput}
        filterMode={filterMode}
        updateNameFilter={updateNameFilter}
        cleanNameFilter={cleanNameFilter}
        setFilterMode={setFilterMode}
        />
    );
}

export default Navigation;