/**
 * Account section at left section of the header.
 */
import * as React from "react";
import Avatar from "@material-ui/core/Avatar";
import ListItemText from "@material-ui/core/ListItemText";
import Badge from "@material-ui/core/Badge";

import Context from "../../context/Context";
import SettingType from "../../redux/types/settingType";

const badgeStyle: React.CSSProperties = {
    float: "left"
}

const avatarStyle: React.CSSProperties = {
    width: "44px",
    height: "44px"
}

const textStyle: React.CSSProperties = {
    float: "left"
}

export default class Account extends React.Component<{}, {}>{
    static contextType = Context;
    render(){
        const setting: SettingType = this.context.setting;
        return (
            <div>
                <Badge 
                color={setting.test ? "secondary" : "primary"} 
                variant="dot" 
                style={badgeStyle}>
                    <Avatar 
                    alt="Avatar" 
                    src="src/image/DEFAULT_AVATAR.png" 
                    style={avatarStyle}/>
                </Badge>
                <ListItemText 
                primary="UserName" 
                secondary="UserID" 
                style={textStyle}/>
            </div>
        );
    }
}