import * as React from "react";
import List from "@material-ui/core/List";
import ListSubheader from "@material-ui/core/ListSubheader";

import Toggle from "../Toggle";
import SettingType from "../../../redux/types/settingType";
import Action from "../../../redux/types/actionTypes";

type Props = {
    addSettingModif: (mods: Partial<SettingType>) => Action,
    getSettVal: (name: string) => any
}

export default class Display extends React.Component<Props, {}>{
    render(){
        const {addSettingModif, getSettVal} = this.props;
        return (
            <React.Fragment>
                <List subheader={<ListSubheader>Test</ListSubheader>}>
                        <Toggle 
                            text="test"
                            handeler={(bool: boolean) => addSettingModif({test: !bool})}
                            checked={getSettVal("test")}
                        />
                </List>
            </React.Fragment>
        );
    }
}