/**
 * Container components
 * See rep components at src\components\App.tsx
 */
import {connect} from "react-redux";

import {State} from "../redux/types/stateType";
import Dispatch from "../redux/types/dispatchType";
import App from "../components/App";

const mapStateToProps = (state: State) => {
    const {pageNavindex} = state.header;
    const {setting, theme} = state.config;
    return {
        pageIndex: pageNavindex,
        setting: setting, 
        theme: theme
    };
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
    /**
     * Change the index on the page nav at middle section of header,
     * and change which page is displayed.
     */
    pageNavOnClick: (index: 0 | 1 | 2) => dispatch({
        type: "CLICK_HEADER_PAGE_NAV",
        payload: {index: index}
    })
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App);