import {Contact} from "../types/stateType";
import Action from "../types/actionTypes";
import {merge} from "./tool/toolFuncs";

export default function reducer(state: Contact, action: Action): Contact{
    switch (action.type){
        case "ADD_USER_TO_CONTACTS":
            return merge(state, {
                users: [
                    ...state.users,
                    action.payload.id
                ]
            });
        default:
            return state;
    }
}