/**
 * Master Component.
 * See container Component at src\containers\app.ts
 */
import * as React from "react";
import Grid from "@material-ui/core/Grid";
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';

import Contact from "./contact/Contact";
import Room from "./room/Room";
import Pages from "./globalComps/Pages";
import Context from "../context/Context";
import Action from "../redux/types/actionTypes";
import SettingType from "../redux/types/settingType";
import ThemeType from "../redux/types/themeType";
import Header from "../containers/header";
import Chat from "../containers/chat";

type Props = {
    pageIndex: 0 | 1 | 2,
    pageNavOnClick: (index: 0 | 1 | 2) => Action,
    setting: SettingType,
    theme: ThemeType
}

const gridStyle: React.CSSProperties = {
    position: "absolute",
    height: "100%"
}

export default class App extends React.Component<Props>{

    /**
     * Change the index of the page nav at the middle section of the section,
     * and also change what page(chat, contact or room) is displayed.
     */
    change_index = (event: React.ChangeEvent<{}>, new_index: 0 | 1 | 2) => {
        this.props.pageNavOnClick(new_index)
    };

    render(){
        const {pageIndex, setting, theme} = this.props;
        return (
            <MuiThemeProvider theme={createMuiTheme({ //Provide the theme for Mui.
                palette: {
                    primary: {
                        main: theme.colorMain
                    },
                    secondary: {
                        main: theme.colorMain
                    }
                }
            })}>
                <Context.Provider value={{ //Provide custom theme and setting.
                    theme: theme,
                    setting: setting
                }}>
                    <Grid container spacing={0} 
                    direction="column" 
                    style={gridStyle}
                    >
                        <Header tabSysIndex={pageIndex} on_change_body={this.change_index}/>
                        <Pages items={[
                            <Chat/>, 
                            <Contact/>, 
                            <Room/>
                        ]} index={pageIndex}/>
                    </Grid>
                </Context.Provider>
            </MuiThemeProvider>
        );
    }
}