import * as React from "react";
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';

import {ChatFilterModes} from "../../../types";
import Action from "../../../redux/types/actionTypes";
import Icon from "../../globalComps/Icon";

type Props = {
    nameFilterInput: string,
    filterMode: ChatFilterModes,
    updateNameFilter: (input: string) => Action,
    cleanNameFilter: () => Action,
    setFilterMode: (mode: ChatFilterModes) => Action,
}

const buttonStyle: React.CSSProperties = {
    padding: 10
}

const dividerStyle: React.CSSProperties = {
    width: 1,
    height: "100%",
    margin: 4
}

const Filter: React.FC<Props> = props => {
    const {nameFilterInput, updateNameFilter} = props;
    return (
        <TextField
        variant="outlined"
        // type="text"
        label="filter"
        value={nameFilterInput}
        onChange={(event: React.ChangeEvent<HTMLInputElement>) => updateNameFilter(event.target.value)}
        InputProps={{
            endAdornment: (
                <InputAdornment
                position="end"
                >
                    <IconButton style={buttonStyle}>
                        <Icon name="chat"/>
                    </IconButton>
                    <Divider style={dividerStyle}/>
                    <IconButton style={buttonStyle}>
                        <Icon name="contact"/>
                    </IconButton>
                </InputAdornment>
            )
        }}
        />
    );
}

export default Filter;