import * as React from "react";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";

import Context from "../../context/Context";
import ThemeType from "../../redux/types/themeType";
import Action from "../../redux/types/actionTypes";
import {Nullable} from "../../types";

type Props = {
    index: 0 | 1 | 2,
    on_change: (event: React.ChangeEvent<{}>, index: 0 | 1 | 2) => void,
    hoverIndex: Nullable<0 | 1 | 2>,
    onEnter: (index: 0 | 1 | 2) => Action,
    onExit: () => Action
};

const typographyStyle = (
    hoverIndex: Nullable<0 | 1 | 2>, 
    index: 0 | 1 | 2, 
    theme: ThemeType
    ): React.CSSProperties => ({
    color: (index === hoverIndex) ? theme.colorMain : undefined
});

export default class TabSys extends React.Component<Props, {}>{
    static contextType = Context;

    render(){
        const {index, on_change, hoverIndex, onEnter, onExit} = this.props;
        const theme: ThemeType = this.context.theme;
        return (
            <Tabs
            value={index}
            onChange={on_change}
            indicatorColor="primary"
            textColor="primary"
            centered
            >
                <Tab 
                disableTouchRipple
                label="CHAT" 
                style={typographyStyle(hoverIndex, 0, theme)}
                onMouseEnter={() => onEnter(0)}
                onMouseLeave={onExit}
                />
                <Tab 
                disableTouchRipple
                label="CONTACT" 
                style={typographyStyle(hoverIndex, 1, theme)}
                onMouseEnter={() => onEnter(1)}
                onMouseLeave={onExit}
                />
                <Tab 
                disableTouchRipple
                label="ROOM" 
                style={typographyStyle(hoverIndex, 2, theme)}
                onMouseEnter={() => onEnter(2)}
                onMouseLeave={onExit}
                />
            </Tabs>
        );
    }
}