import {Chat} from "../types/stateType";
import Action from "../types/actionTypes";
import {merge} from "./tool/toolFuncs";

export default function reducer(state: Chat, action: Action): Chat{
    switch (action.type){
        case "OPEN_USER_IN_CHAT":
            return merge(state, {
                openedUsers: [
                    ...state.openedUsers, 
                    action.payload.id
                ]
            });
        case "SELECT_USER_IN_CHAT":
            return merge(state, {
                selectedUser: action.payload.id,
                selectedIndex: action.payload.index
            });
        case "UPDATE_CHAT_NAME_FILTER":
            return merge(state, {
                nameFilter: action.payload.input
            });
        case "SET_CHAT_FILTER_MODE":
            return merge(state, {
                filterMode: action.payload.enum
            });
        case "UN_SELECT_USER_IN_CHAT":
            return merge(state, {
                selectedUser: null,
                selectedIndex: null
            });
        default:
            return state;
    }
}