/**
 * Master Component.
 * See container Component at src\containers\settingDialog.ts
 */
import * as React from "react";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import Button from "@material-ui/core/Button";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import List from "@material-ui/core/List";
import Grid from "@material-ui/core/Grid";
import { withStyles } from '@material-ui/core/styles';

import ListItem from "../globalComps/ListItem";
import SettPages from "./SettPages/SettPages";
import SettingType from "../../redux/types/settingType";
import ThemeType from "../../redux/types/themeType";
import Action from "../../redux/types/actionTypes";

const styles = {
    dialogPaper: {
        minHeight: '90vh',
        maxHeight: '90vh',
    },
};

function Transition(props: any){ //Slide down animation.
    return <Slide direction="down" {...props} />;
}

type Props = {
    open: boolean,
    on_close: () => Action,
    classes: any,
    nav_index: 0 | 1,
    is_modified: boolean,
    setting_changes: Partial<SettingType>,
    theme_changes: Partial<ThemeType>,
    origin_setting: SettingType,
    origin_theme: ThemeType,
    navOnClick: (index: 0 | 1) => Action,
    addSettingModif: (mods: Partial<SettingType>) => Action,
    addThemeModif: (mods: Partial<ThemeType>) => Action,
    resetTempConfig: () => Action,
    saveTempConfig: (sett_mods: Partial<SettingType>, theme_mods: Partial<ThemeType>) => Action
}

const dialogContent: React.CSSProperties = {
    paddingLeft: "0px"
};

export default withStyles(styles)(class SettingDialog extends React.Component<Props, {}>{

    /**
     * Close the dialog and reset the temporal config copy.
     */
    close_discard = () => {
        const {on_close, resetTempConfig} = this.props;
        resetTempConfig();
        on_close();
    };

    /**
     * Close the dialog, reset and save the temporal config copy.
     */
    close_save = () => {
        const {saveTempConfig, setting_changes, theme_changes} = this.props;
        saveTempConfig(
            setting_changes,
            theme_changes
        );
        this.close_discard();
    };

    /**
     * Get the setting value by setting name.
     * If the setting name exist in temporal cofig copy (means it is modified),
     * then the modified version is return.
     * If not, (means it is not modified), the original one is returned.
     */
    getSettVal = (name: string) => {
        const {setting_changes, origin_setting} = this.props;
        if (name in setting_changes){
            return setting_changes[name];
        }else{
            return origin_setting[name];
        }
    }

    /**
     * Same as getSettVal, but for theme.
     */
    getThemeVal = (name: string) => {
        const {theme_changes, origin_theme} = this.props;
        if (name in theme_changes){
            return theme_changes[name];
        }else{
            return origin_theme[name]
        }
    }

    render(){
        const {
            open, classes, nav_index, navOnClick, is_modified,
            addSettingModif, addThemeModif
        } = this.props;
        return (
            <Dialog
                open={open}
                onClose={this.close_discard}
                TransitionComponent={Transition}
                fullWidth
                maxWidth="sm"
                classes={{paper: classes.dialogPaper}}
            >
                <DialogTitle>SETTING</DialogTitle>
                <DialogContent
                style={dialogContent}
                >
                    <Grid container spacing={24}>
                        <Grid item xs={4}>
                            <List>
                                <ListItem
                                button
                                round_corner
                                primary="Display"
                                selected={nav_index === 0}
                                on_click={() => navOnClick(0)}
                                />
                                <ListItem
                                button
                                round_corner
                                primary="Account"
                                selected={nav_index === 1}
                                on_click={() => navOnClick(1)}
                                />
                            </List>
                        </Grid>
                        <Grid item xs={8}>
                            <SettPages 
                            index={nav_index}
                            addSettingModif={addSettingModif}
                            addThemeModif={addThemeModif}
                            getSettVal={this.getSettVal}
                            getThemeVal={this.getThemeVal}
                            />
                        </Grid>
                    </Grid>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.close_save} color="primary" disabled={! is_modified}>
                        Save
                    </Button>
                    <Button onClick={this.close_discard} color="primary">
                        Cancel
                    </Button>
                </DialogActions>
            </Dialog>
        );
    }
});