/**
 * Container components
 * See rep components at src\components\chat\Chat.tsx
 */
import {connect} from "react-redux";

import {State} from "../redux/types/stateType";
import Dispatch from "../redux/types/dispatchType";
import Chat from "../components/chat/Chat";
import UserType from "../redux/types/userType";
import {StringObject, ChatFilterModes} from "../types";

const mapStateToProps = (state: State) => {
    const {openedUsers, selectedUser, nameFilter, filterMode, selectedIndex} = state.chat;
    const {users} = state.contact;
    return {
        openedUsers: openedUsers,
        selectedUser: selectedUser,
        selectedIndex: selectedIndex,
        nameFilter: nameFilter,
        filterMode: filterMode,
        contacts: users,
        users: state.users
    };
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
    openUser: (id: string, contacts: string[]) => {
        if (contacts.indexOf(id) === -1){
            throw Error("id '" + id + "' doesn't exist in contacts");
        }else{
            dispatch({
                type: "OPEN_USER_IN_CHAT",
                payload: {id: id}
            })
        }
    },
    selectUser: (id: string, index: number, opened: string[]) => {
        if (opened.indexOf(id) === -1){
            throw Error("id '" + id + "' doesn't exist in openedUsers");
        }else{
            dispatch({
                type: "SELECT_USER_IN_CHAT",
                payload: {id: id, index: index}
            })
        }
    },
    unSelectedUser: () => dispatch({
        type: "UN_SELECT_USER_IN_CHAT"
    }),
    updateNameFilter: (input: string) => dispatch({
        type: "UPDATE_CHAT_NAME_FILTER",
        payload: {input: input}
    }),
    cleanNameFilter: () => dispatch({
        type: "UPDATE_CHAT_NAME_FILTER",
        payload: {input: ""}
    }),
    setFilterMode: (mode: ChatFilterModes) => dispatch({
        type: "SET_CHAT_FILTER_MODE",
        payload: {enum: mode}
    }),
    getUserInfo: (id: string, users: StringObject<UserType>): UserType | never => {
        if (id in users){
            return users[id];
        }else{
            throw Error("id '" + id + "' doesn't exist in users");
        }
    },
    getUserInfos: (ids: string[], users: StringObject<UserType>): UserType[] | never => {
        return ids.map((id: string) => {
            if (id in users){
                return users[id];
            }else{
                throw Error("id '" + id + "' doesn't exist in users");
            }
        });
    }
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Chat);