/**
 * This Component is made to make a list item with icon and texts in just one Component.
 */
import * as React from "react";
import ListItem_ from '@material-ui/core/ListItem';
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import Typography from "@material-ui/core/Typography";

type Props = {
    icon?: JSX.Element;
    primary: string | JSX.Element;
    secondary?: string | JSX.Element;
    on_click?: React.ReactEventHandler;
    selected?: boolean;
    button?: boolean;
    round_corner?: boolean;
};

const listItemStyle = (selected: boolean, round_corner: boolean):React.CSSProperties => ({
    backgroundColor: selected ? "#4285f426" : undefined,
    //The hex color above is a transparent version of the main color.
    borderBottomRightRadius: round_corner ? "20px" : "0px",
    borderTopRightRadius: round_corner ? "20px" : "0px"
});

export default class ListItem extends React.Component<Props, {}>{

    render(){
        const {selected, on_click, icon, primary, secondary, children, button, round_corner} = this.props;
        const typographyColor: "primary" | "default" = selected ? "primary" : "default";
        return (
            <ListItem_ 
            button={button} 
            selected={selected} 
            onClick={on_click}
            style={listItemStyle(selected, round_corner)}
            >
                {icon !== undefined &&
                    <ListItemIcon>
                        {icon}
                    </ListItemIcon>
                }
                {(primary !== undefined || secondary !== undefined) &&
                    <ListItemText 
                    primary={
                        <Typography
                        color={typographyColor}
                        >
                            {primary}
                        </Typography>
                    } 
                    secondary={
                        <Typography
                        color={typographyColor}
                        >
                            {secondary}
                        </Typography>
                    }
                    />
                }
                {children}
            </ListItem_>
        );
    }
}