import * as React from "react";

import Pages from "../../globalComps/Pages";
import Display from "./Display";
import Account from "./Account";
import SettingType from "../../../redux/types/settingType";
import ThemeType from "../../../redux/types/themeType";
import Action from "../../../redux/types/actionTypes";

type Props = {
    index: number,
    addSettingModif: (mods: Partial<SettingType>) => Action,
    addThemeModif: (mods: Partial<ThemeType>) => Action,
    getSettVal: (name: string) => any,
    getThemeVal: (name: string) => any
}

export default class SettPages extends React.Component<Props, {}>{
    render(){
        const {index, addSettingModif, getSettVal} = this.props;
        return (
            <Pages items={[
                <Display
                addSettingModif={addSettingModif}
                getSettVal={getSettVal}
                />,
                <Account/>
            ]} index={index}/>
        );
    }
}