import {Header} from "../types/stateType";
import Action from "../types/actionTypes";
import {merge} from "./tool/toolFuncs";

export default function reducer(state: Header, action: Action): Header{
    switch (action.type){
        case "CLICK_HEADER_PAGE_NAV":
            return merge(state, {
                pageNavindex: action.payload.index
            });
        case "HOVER_HEADER_PAGE_NAV":
            return merge(state, {
                pageNavHoverIndex: action.payload.index
            });
        case "HOVER_HEADER_SYS_NAV":
            return merge(state, {
                systemNavHoverIndex: action.payload.index
            });
        default:
            return state
    }
}