/**
 * Container components
 * See rep components at src\components\header\Header.tsx
 */
import {connect} from "react-redux";

import {State} from "../redux/types/stateType";
import Dispatch from "../redux/types/dispatchType";
import Header from "../components/header/Header";

const mapStateToProps = (state: State) => {
    const {pageNavHoverIndex, systemNavHoverIndex} = state.header;
    const {open} = state.settingDialog;
    return {
        tabSysHoverIndex: pageNavHoverIndex,
        systemHoverIndex: systemNavHoverIndex,
        settDialogOpen: open
    };
}

const mapDispatchToProps = (dispatch: Dispatch) => ({
    openSettDialog: () => dispatch({
        type: "TOGGLE_SETTING_DIALOG",
        payload: {bool: true}
    }),
    closeSettDialog: () => dispatch({
        type: "TOGGLE_SETTING_DIALOG",
        payload: {bool: false}
    }),
    enterTabSys: (index: 0 | 1 | 2) => dispatch({
        type: "HOVER_HEADER_PAGE_NAV",
        payload: {index: index}
    }),
    exitTabSys: () => dispatch({
        type: "HOVER_HEADER_PAGE_NAV",
        payload: {index: null}
    }),
    enterSysIcon: (index: 0 | 1) => dispatch({
        type: "HOVER_HEADER_SYS_NAV",
        payload: {index: index}
    }),
    exitSysIcon: () => dispatch({
        type: "HOVER_HEADER_SYS_NAV",
        payload: {index: null}
    })
})

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Header);