/**
 * Master Component.
 * See container Component at src\containers\header.ts
 */
import * as React from "react";
import Grid from "@material-ui/core/Grid";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";

import TabSys from "./TabSys";
import Account from "./Account";
import System from "./System";
import Action from "../../redux/types/actionTypes";
import {Nullable} from "../../types";

type Props = {
    tabSysIndex: 0 | 1 | 2,
    on_change_body: (event: React.ChangeEvent<{}>, index: 0 | 1 | 2) => void,
    tabSysHoverIndex: Nullable<0 | 1 | 2>,
    systemHoverIndex: Nullable<0 | 1>,
    settDialogOpen: boolean,
    openSettDialog: () => Action,
    closeSettDialog: () => Action,
    enterTabSys: (index: 0 | 1 | 2) => Action,
    exitTabSys: () => Action,
    enterSysIcon: (index: 0 | 1) => Action,
    exitSysIcon: () => Action
}

const gridStyle: React.CSSProperties = {
    position: "absolute",
    flexGrow: 1
}

const Header: React.FC<Props> = props => {
    const {
        tabSysIndex, on_change_body, tabSysHoverIndex, enterTabSys, exitTabSys,
        settDialogOpen, openSettDialog, closeSettDialog, enterSysIcon, exitSysIcon,
        systemHoverIndex
    } = props;
    return (
        <Grid item container xs spacing={0} style={gridStyle}>
            <AppBar position="static" color="inherit">
                <Toolbar>
                    <Grid item xs={3}>
                        <Account/>
                    </Grid>
                    <Grid item xs={6}>
                        <TabSys 
                        index={tabSysIndex} 
                        on_change={on_change_body}
                        hoverIndex={tabSysHoverIndex}
                        onEnter={enterTabSys}
                        onExit={exitTabSys}
                        />
                    </Grid>
                    <Grid item xs={3}>
                        <System
                        setting_open={settDialogOpen}
                        hoverIndex={systemHoverIndex}
                        onEnter={enterSysIcon}
                        onExit={exitSysIcon}
                        onCloseSett={closeSettDialog}
                        onOpenSett={openSettDialog}
                        />
                    </Grid>
                </Toolbar>
            </AppBar>
        </Grid>
    );
};

export default Header;

