import * as React from "react";
import IconButton from "@material-ui/core/IconButton";
import Badge from "@material-ui/core/Badge";

import Icon from "../globalComps/Icon";
import SettingDialog from "../../containers/settingDialog";
import Action from "../../redux/types/actionTypes";
import {Nullable} from "../../types";

type Props = {
    setting_open: boolean,
    hoverIndex: Nullable<0 | 1>,
    onEnter: (index: 0 | 1) => Action,
    onExit: () => Action,
    onOpenSett: () => Action,
    onCloseSett: () => Action
}

export default class System extends React.Component<Props, {}>{
    render(){
        const {setting_open, hoverIndex, onEnter, onExit, onCloseSett, onOpenSett} = this.props;
        return (
            <div style={{
                float: "right"
            }}>
                <IconButton 
                aria-label="Setting" 
                color={(hoverIndex === 0) ? "primary" : "default"} 
                onClick={onOpenSett} 
                onMouseEnter={() => onEnter(0)}
                onMouseLeave={onExit}
                style={{
                    marginRight: "16px"
                }}
                >
                    <Icon name="setting"/>
                </IconButton>

                <IconButton 
                aria-label="Notification" 
                color={(hoverIndex === 1) ? "primary" : "default"}
                onMouseEnter={() => onEnter(1)}
                onMouseLeave={onExit}
                >
                    <Badge color="primary" badgeContent={1}>
                        <Icon name="notif"/>
                    </Badge>
                </IconButton>

                <SettingDialog open={setting_open} on_close={onCloseSett}/>
            </div>
        );
    }
}