/**
 * Master Component.
 */
import * as React from "react";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";

import Pages from "../globalComps/Pages";
import NoChatInfo from "./NoChatInfo";
import UserType from "../../redux/types/userType";
import Action from "../../redux/types/actionTypes";
import {ChatFilterModes, StringObject, Nullable} from "../../types";
import Navigation from "./navigation/Navigation";

type Props = {
    openedUsers: string[],
    selectedUser: Nullable<string>,
    selectedIndex: Nullable<number>,
    nameFilter: string,
    filterMode: ChatFilterModes,
    contacts: string[],
    users: StringObject<UserType>,
    openUser: (id: string, contacts: string[]) => Action,
    selectUser: (id: string, index: number, opened: string[]) => Action,
    unSelectedUser: () => Action,
    updateNameFilter: (input: string) => Action,
    cleanNameFilter: () => Action,
    setFilterMode: (mode: ChatFilterModes) => Action,
    getUserInfo: (id: string, users: StringObject<UserType>) => UserType | never,
    getUserInfos: (ids: string[], users: StringObject<UserType>) => UserType[] | never
}

const gridStyle: React.CSSProperties = {
    marginTop: 64 //64 as in the height of the header.
}

const paperStyle: React.CSSProperties = {
    width: "100%"
}

const Chat: React.FC<Props> = props => {
    const {nameFilter, filterMode, updateNameFilter, cleanNameFilter, setFilterMode} = props;
    return (
        <Grid item container xs style={gridStyle}>
            <Grid item container xs={3}>
                <Paper square={true} style={paperStyle}>
                    <Navigation
                    nameFilterInput={nameFilter}
                    filterMode={filterMode}
                    updateNameFilter={updateNameFilter}
                    cleanNameFilter={cleanNameFilter}
                    setFilterMode={setFilterMode}
                    />
                </Paper>
            </Grid>
            <Grid item container xs={9}>
                <Paper square={true} elevation={1} style={paperStyle}>
                    <Pages items={[
                        <NoChatInfo/>
                    ]} index={0}/>
                </Paper>
            </Grid>
        </Grid>
    );
}

export default Chat;